
import React from "react";
import { ButtonGroup, Button, Modal, Form } from 'react-bootstrap';

import {
  GetCommonData, InsertData, UpdateData
} from "../store/actions/CommonAction";
import {
  addRecord
} from "../store/actions/CommonMongoAction";

import { If, Then } from 'react-if';
//import { If, Then, Else, When, Unless, Switch, Case, Default } from 'react-if';

import { connect } from "react-redux";
// import { Link } from "react-router-dom";
import DataTable from './Common/DataTableWithFilter';
import DropDown from './Common/DropDownList';
import AlertBox from './Common/AlertBox';
import { CompareJson, fnBindRootData, fnRenderfrmControl, fnDatatableCol, fnCleanData, GetJsonToArray, getMax, joinObject } from '../utility/utility.jsx';
// reactstrap components
import {
  Card,
  CardHeader,
  CardBody,
  CardTitle,
  Table,
  Row,
  Col
} from "reactstrap";
import { getuser } from "utility/utility";

class GradeRulesMapping_Allocation extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoaded: false,
      showModal: false,
      items: [],
      store: [],
      activePage: 1,
      root: "vwGradeRuleMapping",
      PageTitle: "Grade Rules Mapping",
      FormTitle: "",
      formvalue: {},
      event: "",
      ModalValueChanged: false,
      user: {}
      //selectedrow: { "Id": 1, "type": "Job", "alerttitle": "Mongoquery2", "querytype": "MongoQuery", "databasename": "mydb", "collection": "movie", "dbserver": "Replica", "tomail": "jitendras@policybazaar.com,Ayush@policybazaar.com", "tomobile": "99688436", "count": 4, "operation": "lt", "starttime": "1970-01-01T10:28:04.000Z", "frequency": 120, "IsActive": false, "reporttype": null, "endtime": "1970-01-01T10:28:04.000Z", "nextruntime": "2019-11-29T11:20:00.960Z", "createddate": null, "createdby": "deepankar", "updateddate": null, "updatedby": null, "ExcelSheetNames": "BaseData,Health,Term,Investment,Motor,EMAILBODY,EMAILBODY", "querysource": "{$query:{name:\"garvitjain\",count:{$lt:6}}}", "ccmail": null, "bccmail": null, "frommail": null }
    };
    this.handleClose = this.handleClose.bind(this);
    this.handleShow = this.handleShow.bind(this);
    this.handleSave = this.handleSave.bind(this);
    this.productchange = this.productchange.bind(this);
    this.selectedrow = { "Id": 0, "Grade": "", "IsActive": false, "CreatedOn": new Date(), "RuleID": null, "GradeDistribution": null }
    this.columnlist = [
      {
        name: "Id",
        label: "Id",
        type: "hidden",
        hide: true,
      },
      {
        name: "AGR.ProductId",
        label: "productid",
        alias: "productid",
        type: "hidden",
        hide: true,
      },
      {
        name: "GRM.RuleID",
        label: "Rule Name",
        alias: "RuleID",
        type: "dropdown",
        config: {
          root: "AgentRules",
          cols: ["RuleID AS Id", "RuleName AS Display"],
          //con: [{ "ISActive": 1 }],
          state: true,
          statename: "AgentRules_vwGradeRuleMapping"
        },
        searchable: true
      },
      {
        name: "Grade",
        label: "Grade",
        type: "number",
        searchable: true
      },
      {
        name: "GradeDistribution",
        label: "GradeDistribution",
        type: "number",
        searchable: true
      },
      {
        name: "MinAgentCount",
        label: "MinAgentCount",
        type: "number",
        searchable: true
      },
      {
        name: "GRM.ISActive",
        label: "ISActive",
        alias: "ISActive",
        type: "bool"
      }

    ];
    let count = 0;
    this.ProductList = {
      config:
      {
        root: "Products",
        cols: ['id AS Id', 'ProductName AS Display'],
        con: [{ "ISActive": true }]        
      }
    }
  }



  componentDidMount() {

    this.columnlist.map(col => (
      fnBindRootData(col, this.props)
    ));
    this.props.GetCommonData({
      limit: 10,
      skip: 0,
      root: this.state.root,
      cols: GetJsonToArray(this.columnlist, "name"),
      c: "L",
      con: [{ "AGR.IsActive": 1 }]
    });
    setTimeout(function () {
      this.setState({ user: getuser() });
    }.bind(this), 500);

  }

  fnBindStore(col, nextProps) {
    if (col.type == "dropdown") {
      let items;
      if (nextProps.CommonData[this.state.root] && nextProps.CommonData[col.config.root]) {
        items = joinObject(nextProps.CommonData[this.state.root], nextProps.CommonData[col.config.root], col.alias ? col.alias : col.name)
        this.setState({ items: items });
      }
    }
  }

  componentWillReceiveProps(nextProps) {

    if (!nextProps.CommonData.isError) {
      this.setState({ items: nextProps.CommonData[this.state.root] });
      this.setState({ store: nextProps.CommonData });
      this.columnlist.map(col => (
        this.fnBindStore(col, nextProps)
      ));
    }



    if (nextProps.CommonData && nextProps.CommonData.InsertSuccessData && nextProps.CommonData.InsertSuccessData.status) {
      if (nextProps.CommonData.InsertSuccessData.status != 200) {
        //alert(nextProps.CommonData.InsertSuccessData.error);
        this.setState({ showAlert: true, AlertMsg: nextProps.CommonData.InsertSuccessData.error, AlertVarient: "danger" });
      }
      else {
        //this.setState({ showModal: false });
        this.setState({ showAlert: true, AlertMsg: "Record Add Successfully.", AlertVarient: "success" });
      }
    }

    if (nextProps.CommonData && nextProps.CommonData.UpdateSuccessData && nextProps.CommonData.UpdateSuccessData.status) {
      if (nextProps.CommonData.UpdateSuccessData.status != 200)
        this.setState({ showAlert: true, AlertMsg: nextProps.CommonData.InsertSuccessData.error, AlertVarient: "danger" });
      else {
        //this.setState({ showModal: false });
        this.setState({ showAlert: true, AlertMsg: "Record Updated Successfully.", AlertVarient: "success" });
      }
    }

  }


  fnDatatableCol() {

    var columns = fnDatatableCol(this.columnlist);

    // columns.push({
    //   name: "Products Name",
    //   selector: "ProductID_display",
    //   sortable: true,
    // });

    columns.push({
      name: "Action",
      cell: row => <ButtonGroup aria-label="Basic example">
        <Button variant="secondary" onClick={() => this.handleCopy(row)}><i className="fa fa-files-o" aria-hidden="true"></i></Button>
        <Button variant="secondary" onClick={() => this.handleEdit(row)}><i className="fa fa-pencil-square-o" aria-hidden="true"></i></Button>
      </ButtonGroup>
    });
    return columns;
  }
  handleCopy(row) {
    this.setState({ formvalue: Object.assign({}, row, {}), event: "Copy", showModal: true, FormTitle: "Copy Record" });
  }
  handleEdit(row) {
    this.setState({ od: Object.assign({}, row, {}), formvalue: Object.assign({}, row, {}), event: "Edit", showModal: true, FormTitle: "Edit Record" });
  }
  handleClose() {
    this.setState({ showModal: false });
  }
  handleShow() {
    this.setState({ formvalue: this.selectedrow, event: "Add", showModal: true, FormTitle: "Add New Record" });
  }

  handleSave() {
    if (document.getElementsByName("frmGradeRulesMapping_Allocation").length > 0 &&
      document.getElementsByName("frmGradeRulesMapping_Allocation")[0].reportValidity()) {

      let formvalue = JSON.parse(JSON.stringify(this.state.formvalue));
      this.fnCleanData(formvalue);
      let id = formvalue["Id"];

      delete formvalue["Id"];

      if (this.state.event == "Edit") {
        this.fnCleanData(formvalue);
        let res = this.props.UpdateData({
          root: "GradeRulesName",
          body: formvalue,
          querydata: { "Id": id },
          c: "L"
        });

        this.props.addRecord({
          root: "History",
          body: {
            module: "GradeRulesName",
            od: this.state.od,
            nd: formvalue,
            ts: new Date(),
            by: getuser().UserId
          }
        });
      } else if (this.state.event == "Copy") {
        //formvalue["Id"] = getMax(this.state.items, "Id").Id + 1;
        formvalue["CreatedOn"] = new Date();
        this.fnCleanData(formvalue);
        this.props.InsertData({
          root: "GradeRulesName",
          body: formvalue,
          c: "L"
        });


      } else {
        //formvalue["Id"] = getMax(this.state.items, "Id").Id + 1;
        formvalue["CreatedOn"] = new Date();
        this.fnCleanData(formvalue);
        this.props.InsertData({
          root: "GradeRulesName",
          body: formvalue,
          c: "L"
        });
      }
      let productid = formvalue["productid"];

      setTimeout(function () {

        if (productid) {
          this.props.GetCommonData({
            root: this.state.root,
            cols: GetJsonToArray(this.columnlist, "name"),
            c: "L",
            con: [{ "AGR.ProductID": productid },{ "AGR.IsActive": 1 }],
          });
        }
        else {
          this.props.GetCommonData({
            limit: 10,
            skip: 0,
            root: this.state.root,
            cols: GetJsonToArray(this.columnlist, "name"),
            c: "L",
            con: [{ "AGR.IsActive": 1 }]
          });
        }
      }.bind(this), 2000);




      this.setState({ showModal: false });
    }
    return false;
  }
  handleChange = (e, props) => {
    debugger;
    let formvalue = this.state.formvalue;
    let od = this.state.od;

    if (e.target && e.target.type == "checkbox") {
      formvalue[e.target.id] = e.target.checked;
    }
    else if (e._isAMomentObject) {
      formvalue[props] = e.format()
    }
    else {
      formvalue[e.target.id] = e.target.value;
    }
    //debugger;
    //let Changed = CompareJson(od, formvalue);

    this.setState({ formvalue: formvalue, ModalValueChanged: true });
  }

  fnCleanData(formvalue) {
    formvalue = fnCleanData(this.columnlist, formvalue);
    this.setState({ formvalue: formvalue });
  }

  productchange(e, props) {

    this.props.GetCommonData({
      limit: 10,
      skip: 0,
      root: this.state.root,
      cols: ['GRM.id AS Id', 'GRM.RuleID AS RuleID', 'AGR.ProductId as productid', 'GRM.Grade AS Grade', 'GRM.GradeDistribution AS GradeDistribution', 'GRM.MinAgentCount AS MinAgentCount'
        , 'GRM.CreatedOn AS CreatedOn', 'GRM.IsActive AS ISActive'],
      c: "L",
      con: [{ "ProductID": e.target.value },{ "AGR.IsActive": 1 }],
    });

  }

  render() {

    const columns = this.fnDatatableCol();
    const { items, PageTitle, showModal, FormTitle, formvalue, showAlert, AlertMsg, AlertVarient, ModalValueChanged } = this.state;
    //console.log("columns", columns);
    //console.log("items", items);

    return (
      <>
        <div className="content">
          <AlertBox show={showAlert} variant={AlertVarient} body={AlertMsg}></AlertBox>
          <Row>
            <Col md="12">
              <Card>
                <CardHeader>
                  <Row>
                    <Col md={7}>
                      <CardTitle tag="h4">{PageTitle}</CardTitle>
                    </Col>
                    <Col md={4}>
                      <Form.Group as={Col} md={12} controlId="product_dropdown">

                        <DropDown firstoption="Select Product" col={this.ProductList} onChange={this.productchange}>
                        </DropDown>
                      </Form.Group>
                    </Col>
                    <Col md={1}>
                      <Button variant="primary" onClick={this.handleShow}>ADD</Button>
                    </Col>
                  </Row>
                </CardHeader>

                <CardBody>
                  <DataTable
                    columns={columns}
                    data={items}
                  />
                </CardBody>
              </Card>
            </Col>
          </Row>

          <Modal show={showModal} onHide={this.handleClose} dialogClassName="modal-90w">
            <Modal.Header closeButton>
              <Modal.Title>{FormTitle}</Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <Form name="frmGradeRulesMapping_Allocation">
                <Row>
                  {this.columnlist.map(col => (
                    fnRenderfrmControl(col, formvalue, this.handleChange, this.state.event)
                  ))}
                </Row>
              </Form>
            </Modal.Body>
            <Modal.Footer>
              <Button variant="secondary" onClick={this.handleClose}>
                Close
          </Button>
              <If condition={ModalValueChanged}>
                <Then>
                  <input type="submit" value="Save Changes" className="btn btn-primary" onClick={this.handleSave} />
                </Then>
              </If>
            </Modal.Footer>
          </Modal>
        </div>
      </>
    );
  }
}


function mapStateToProps(state) {
  return {
    CommonData: state.CommonData
  };
}

export default connect(
  mapStateToProps,
  {
    GetCommonData,
    InsertData,
    UpdateData,
    addRecord
  }
)(GradeRulesMapping_Allocation);